.main_container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
}

.row {
  display: flex;
}

.form_Container {
  width: 100%;
}

.textInput {
  padding: 10px;
  background-color: #fff;
  border: 1px solid #f6f6f6;
}

.textInputContainer {
  margin: 10px;
}

.submitButton {
  cursor: pointer;
  padding: 10px;
  background-color: green;
  border-radius: 10px;
  color: #fff;
}

.results {
  color: blue;
}
.links {
  justify-content: space-between;
  align-items: center;
  display: flex;
}
.first_button_figmal {
  padding: 10px;
  color: #000000;
  font-size: 18px;
  border-radius: 15px;
  background-color: aqua;
  cursor: pointer;
  margin: 10px;
}
.first_button_figmal:hover {
  background-color: rgb(184, 189, 189);
}
.second_button_chat {
  padding: 10px;
  color: #000000;
  font-size: 18px;
  border-radius: 15px;
  background-color: aqua;
  cursor: pointer;
  margin: 10px;
}
.second_button_chat:hover {
  background-color: rgb(184, 189, 189);
}
.adds{
    justify-content: center;
    align-items: center;
    font-size: 26px;
}
